.footer {
  display: flex;

  align-items: center;

  flex-direction: column;

  background: white;

  width: 100%;
  height: fit-content;

  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);

  box-shadow: 0 10px 20px rgba(31, 32, 65, 0.1);

  flex-grow: 0;
}

.footer_width_narrow {
  background: #e5e5e5;
}

.footer__body {
  max-width: 1160px;
  width: 80.56%;
  height: 272px;

  padding-top: 100px;

  display: grid;
  grid-template-rows: 10.75% 10.22% 10.22% 10.22% auto;
  grid-template-columns: 25.86% 17.24% 17.24% 17.24% auto;
  grid-template-areas:
          'logo nav-header about-header help-header subscription-header'
          'logo about about-service agreements subscription-paragraph'
          'paragraph news team communities subscription-paragraph'
          'footer__paragraph help vacancy contacts subscription'
          'empty service investors empty empty';
}

.footer__logo {
  grid-area: logo;
}

.footer__paragraph {
  grid-area: paragraph;
}

.footer__nav-header {
  grid-area: nav-header;
}

.footer__about {
  grid-area: about;
}

.footer__news {
  grid-area: news;
}

.footer__help {
  grid-area: help;
}

.footer__service {
  grid-area: service;
}

.footer__about-header {
  grid-area: about-header;
}

.footer__about-service {
  grid-area: about-service;
}

.footer__team {
  grid-area: about-service;
}

.footer__vacancy {
  grid-area: vacancy;
}

.footer__investors {
  grid-area: investors;
}

.footer__help-header {
  grid-area: help-header;
}

.footer__help-agreements {
  grid-area: help-header;
}

.footer__communities {
  grid-area: communities;
}

.footer__contacts {
  grid-area: contacts;
}

.footer__subscription-header {
  grid-area: subscription-header;
}

.footer__subscription-paragraph {
  grid-area: subscription-paragraph;
}

.footer__subscription {
  grid-area: subscription-paragraph;
}
